
#app {
  margin-bottom: 60px;
}
body .vux-no-group-title {
  margin-top: 0.77em;
}
body .weui-cells,
body .weui-toast__content,
body .router-link-active {
  font-size: 14px;
}
body .weui-btn {
  font-size: 15px;
}
body .weui-btn_mini {
  font-size: 13px;
  min-width: 101px;
}
body .vux-tab .vux-tab-item.vux-tab-selected {
  color: #ffa500;
}
body .weui-btn_primary, body .vux-tab-ink-bar,
body .weui-btn_primary:not(.weui-btn_disabled):active {
    background-color: #ffa500;
}
body .weui-btn_disabled.weui-btn_primary {
  background-color:#777;
}
body .weui-cells__title {
  margin: 0.77em 0;
}
body .vux-popup-dialog {
  overflow: hidden;
}
body .vux-popup-header-right, body .dp-header .dp-item.dp-right {
  color: orange;
}
body .clearfix:after {
  content: '';
  display: block;
  clear: both;
  height: 0;
  visibility: hidden;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
.list-complete-item {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.list-complete-enter {
  opacity: 0;
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
}
.list-complete-leave-to {
  opacity: 0;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
}
.list-complete-leave-active {
  position: absolute;
}



